<template>
  <div id="service_page" class="service_page" onselectstart="return false" ondragstart="return false">


    <div class="main_part">

      <div class="title_part">
        <div :class="'title_'+global.lang">{{ global.lang_pack.service }}</div>
      </div>

      <div :class="'content_text_'+global.lang" style="margin-top: 80px; margin-bottom: 40px;">{{ global.lang_pack.service_intro }}</div>

      <div class="subsubtitle_box">
        <div class="grey_line"></div>
        <div class="subtitle_line" @click="expend(0)">
          <div style="display: flex; flex-direction: row; align-items: center">
            <img class="alert" style="height: 60px;" src="static/about/alert.svg" v-if="global.mode === 0"/>
            <div class="subsubtitle_num"  v-if="global.mode === 0">01</div>
            <div class="subsubtitle_text">
              {{ global.lang_pack.sign_design }}
            </div>
          </div>

          <img class="add add_0" src="static/about/arrow.svg"/>
        </div>

        <div class="expand_part_box part_0">
          <div class="expand_part" id="part_0">
            <div :class="'content_text_'+global.lang">{{ global.lang_pack.sign_design_intro }}</div>
            <div class="blank_box" style="height: 80px;"></div>
          </div>
        </div>
      </div>

      <div class="subsubtitle_box">
        <div class="grey_line"></div>
        <div class="subtitle_line" @click="expend(1)">
          <div style="display: flex; flex-direction: row; align-items: center">
            <img class="alert" style="height: 60px;" src="static/about/alert.svg"  v-if="global.mode === 0"/>
            <div class="subsubtitle_num" v-if="global.mode === 0">02</div>
            <div class="subsubtitle_text">
              {{ global.lang_pack.plane_vision }}
            </div>
          </div>

          <img class="add add_1" src="static/about/arrow.svg"/>
        </div>

        <div class="expand_part_box part_1">
          <div class="expand_part" id="part_1">
            <div :class="'content_text_'+global.lang">{{ global.lang_pack.plane_vision_intro }}</div>
            <div class="blank_box" style="height: 80px;"></div>
          </div>
        </div>
      </div>

      <div class="subsubtitle_box">
        <div class="grey_line"></div>
        <div class="subtitle_line" @click="expend(2)">
          <div style="display: flex; flex-direction: row; align-items: center">
            <img class="alert" style="height: 60px;" src="static/about/alert.svg"  v-if="global.mode === 0"/>
            <div class="subsubtitle_num" v-if="global.mode === 0">03</div>
            <div class="subsubtitle_text">
              {{ global.lang_pack.logo_package }}
            </div>
          </div>

          <img class="add add_2" src="static/about/arrow.svg"/>
        </div>

        <div class="expand_part_box part_2">
          <div class="expand_part" id="part_2">
            <div :class="'content_text_'+global.lang">{{ global.lang_pack.plane_vision_intro }}</div>
            <div class="blank_box" style="height: 80px;"></div>
          </div>
        </div>
      </div>

      <div class="subsubtitle_box">
        <div class="grey_line"></div>
        <div class="subtitle_line" @click="expend(3)">
          <div style="display: flex; flex-direction: row; align-items: center">
            <img class="alert" style="height: 60px;" src="static/about/alert.svg" v-if="global.mode === 0"/>
            <div class="subsubtitle_num" v-if="global.mode === 0">04</div>
            <div class="subsubtitle_text">
              {{ global.lang_pack.brand_strategy }}
            </div>
          </div>

          <img class="add add_3" src="static/about/arrow.svg"/>
        </div>

        <div class="expand_part_box part_3">
          <div class="expand_part" id="part_3">
            <div :class="'content_text_'+global.lang">{{ global.lang_pack.brand_strategy_intro }}</div>
            <div class="blank_box" style="height: 80px;"></div>
          </div>
        </div>
      </div>

      <div class="grey_line"></div>

    </div>

    <Footer></Footer>
    <LangTransfer></LangTransfer>
    <Menu></Menu>
    <Logo></Logo>
  </div>
</template>

<style lang="css" scoped>

div.service_page {
  position: absolute;
  background: #f7f7f7;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
}

  div.main_part{
  width: 50%;
  margin-left: 38.02%;
}

div.title_part{
  margin-top: 260px;
  margin-left: -499.968rem;
}

div.grey_line{
  position: relative;
  height:3px;
  transform:scale(1 , 0.5);
  transform-origin: bottom;
  background: #37383d;
}

div.subtitle_line{
  display: flex;
  flex-direction: row;
  padding-bottom: 20px;
  padding-top: 20px;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

div.subsubtitle_num {
  color: #efcd24;
  font-size: 30px;
  line-height: 60px;
  margin-left: -51px;
  margin-right: 30px;
  margin-top: 18px;
}

div.subsubtitle_text {
  position: relative;
  letter-spacing: 3px;
  z-index: 1;
  font-size: 40px;
  margin-top: 15px;
}

div.expand_part_box{
  overflow: hidden;
}

img.add{
  height: 30px;
}

div.part_0{
  height: 0;
}

div.part_1{
  height: 0;
}

div.part_2{
  height: 0;
}

div.part_3{
  height: 0;
}

/*手机版样式*/
@media screen and (max-width: 540px) {
  div.main_part{
    width: 700rem;
    margin-left: 25rem;
    margin-top: 120px;
  }

  div.title_part {
    margin-top: 110px;
    margin-left: unset;
    margin-bottom: 110px;
  }

  div.grey_line {
    height:3px;
    transform:scale(1 , 0.5);
    background: #898989;
    width: 700rem;
  }

  div.subtitle_line {
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    padding-top: 35px;
    padding-bottom: 35px;
  }

  div.subsubtitle_box {
    margin-top: 0;
  }

  div.subsubtitle_text {
    position: relative;
    margin-top: 0;
    letter-spacing: 2px;
    z-index: 0;
    font-size: 20px;
    font-family: blk_en;
  }

  div.subsubtitle_num {
    position: relative;
    z-index: 0;
    letter-spacing: 2px;
    font-family: blk_en;
    color: #efcd24;
    font-size: 50px;
    line-height: 50px;
    margin-top: unset;
    margin-left: 0;
  }

  img.add{
    height: 30rem;
  }
}

</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer";
import Footer from "../../components/footer/Footer";
import Logo from "../../components/logo/Logo";

let has_expend_anime = false
//export default
export default {
  name: "About",
  data() {
    return {
      global: global,
      dots:"",
    };
  },
  components: {
    Menu,
    LangTransfer,
    Footer,
    Logo
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.refresh_windows_size();
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
    let that = this
  },
  destroyed() {
  },
  watch: {
    "global.lang":function (val, oldVal){
      this.refresh_page()
    }
  },
  methods: {
    expend(idx){
      if(has_expend_anime)
        return
      has_expend_anime = true
      let height = document.getElementById("part_"+idx).clientHeight
      let expend_height = document.getElementsByClassName("part_"+idx)[0].clientHeight
      anime({
        targets: "div.part_"+idx,
        height: expend_height === 0 ? height: 0,
        easing: "linear",
        duration: height / 2, // 调整为根据当前高度或调整
        complete:function(){
          has_expend_anime = false
        }
      });

      anime({
        targets: "img.add_"+idx,
        rotate: expend_height === 0 ? -90: 0,
        easing: "linear",
        duration: 200, // 调整为根据当前高度或调整
      })
    },

    refresh_page(){
      for(let i = 0; i < document.getElementsByClassName("expand_part").length; i++){
        let expend_height = document.getElementsByClassName("part_"+i)[0].clientHeight
        if(expend_height !== 0){
          has_expend_anime = false
          this.expend(i)
        }
      }
    }
  }
};
</script>



